<template>
  <div class="map" ref="map">
    <div class="mapBox" ref="mapBox" @mousedown="onMousedown"></div>
    <!-- 中心点十字架 -->
    <div class="line lineX"></div>
    <div class="line lineY"></div>
    <!-- 搜索框 -->
    <div class="searchBox">
      <div class="searchInput">
        <input
          type="text"
          placeholder="搜索..."
          v-model="searchText"
          @input="onSearch"
          @keyup.enter="onSearch"
          @click.stop
        />
      </div>
      <div class="searchList">
        <div
          class="searchItem"
          v-for="(item, index) in searchResultList"
          :key="index"
          @click="go(item.location)"
        >
          {{ item.name }}
        </div>
      </div>
    </div>
    <!-- 经纬度框 -->
    <div class="posInput">
      <input
        type="text"
        placeholder="经纬度..."
        v-model="posValue"
        @keyup.enter="onChangePos"
        @click.stop
      />
    </div>
    <!-- 返回到初始中心点按钮 -->
    <div class="backCenterBtn" @click.stop="backCenter"></div>
    <!-- 放大缩小按钮 -->
    <div class="scaleBtnBox">
      <div
        class="scaleBtn in"
        :class="{ disabled: zoom >= maxZoom }"
        @click.stop="zoomIn"
      ></div>
      <div
        class="scaleBtn out"
        :class="{ disabled: zoom <= minZoom }"
        @click.stop="zoomOut"
      ></div>
    </div>
  </div>
</template>

<script>
import { animate, easeOut } from "popmotion";
import Konva from "konva";
import {
  getTileUrl,
  TILE_SIZE,
  getTileRowAndCol,
  lngLat2Mercator,
  getPxFromLngLat,
  resolutions,
  mercatorToLngLat,
  KEY,
} from "./utils";

// 瓦片类
class Tile {
  constructor({ layer, row, col, zoom, x, y, shouldRender }) {
    // 瓦片显示图层
    this.layer = layer;
    // 瓦片行列号
    this.row = row;
    this.col = col;
    // 瓦片层级
    this.zoom = zoom;
    // 显示位置
    this.x = x;
    this.y = y;
    // 判断瓦片是否应该渲染
    this.shouldRender = shouldRender;
    // 瓦片url
    this.url = "";
    // 缓存key
    this.cacheKey = this.row + "_" + this.col + "_" + this.zoom;
    // 瓦片图片
    this.img = null;
    // 瓦片透明度
    this.opacity = 0;
    // 图片是否加载完成
    this.loaded = false;
    // 图片加载超时定时器
    this.timer = null;
    // 瓦片渐现过渡时间
    this.fadeInDuration = 400;

    this.createUrl();
    this.load();
  }

  // 生成url
  createUrl() {
    this.url = getTileUrl(this.row, this.col, this.zoom);
  }

  // 加载图片
  load() {
    let img = new Image();
    img.src = this.url;
    // 加载超时，重新加载
    this.timer = setTimeout(() => {
      this.createUrl();
      this.load();
    }, 1000);
    // 加载完成
    img.onload = () => {
      clearTimeout(this.timer);
      // 创建图片元素
      this.img = new Konva.Image({
        image: img,
        width: TILE_SIZE,
        height: TILE_SIZE,
        opacity: this.opacity,
      });
      this.loaded = true;
      this.render();
    };
  }

  // 渲染
  render(isFadeIn = false) {
    if (!this.loaded || !this.img || !this.shouldRender(this.cacheKey)) {
      return;
    }
    // 添加到图层
    this.layer.add(this.img);
    // 设置显示位置
    this.img.x(this.x).y(this.y);
    // 需要渐现
    if (isFadeIn && this.opacity !== 0) {
      this.hide();
    }
    this.fadeIn();
  }

  // 渐现
  fadeIn() {
    if (this.opacity >= 1 || !this.img) {
      return;
    }
    let base = this.opacity;
    let anim = new Konva.Animation((frame) => {
      let opacity = (frame.time / this.fadeInDuration) * 1 + base;
      this.opacity = opacity;
      this.img.opacity(opacity);
      if (opacity >= 1) {
        anim.stop();
      }
    }, this.layer);
    anim.start();
  }

  // 隐藏
  hide() {
    if (!this.img) {
      return
    }
    this.opacity = 0;
    this.img.opacity(0);
  }

  // 更新要添加到的图层
  updateLayer(layer) {
    this.layer = layer;
    return this;
  }

  // 更新位置
  updatePos(x, y) {
    this.x = x;
    this.y = y;
    return this;
  }
}

export default {
  name: "App",
  data() {
    return {
      // 画布宽高
      width: 0,
      height: 0,
      // 鼠标按下标志
      isMousedown: false,
      lastMouseTime: null,
      lastDuration: 0,
      lastDistance: [],
      // 缓存瓦片实例
      tileCache: {},
      // 记录当前画布上需要的瓦片
      currentTileCache: {},
      // 初始中心经纬度
      initCenter: [120.148732, 30.231006], // 雷锋塔
      center: [120.148732, 30.231006], // 雷锋塔
      // 初始缩放层级
      zoom: 14,
      // 缩放层级范围
      minZoom: 3,
      maxZoom: 18,
      // 缩放相关
      lastZoom: 0,
      scale: 1,
      scaleTmp: 1,
      playback: null,
      // 图层相关
      stage: null,
      layer1: null,
      layer2: null,
      useLayer1: true,
      // 搜索&移动
      searchText: "",
      searchTimer: null,
      searchResultList: [],
      translate: [0, 0],
      translateTmp: [0, 0],
      translatePlayback: null,
      // 经纬度框
      posValue: "",
    };
  },
  watch: {
    center(val) {
      this.posValue = val.join(",");
    },
  },
  async mounted() {
    await this.location();
    this.init();
    this.renderTiles();
    window.addEventListener("mousemove", this.onMousemove);
    window.addEventListener("mouseup", this.onMouseup);
    window.addEventListener("wheel", this.onMousewheel);
  },
  beforeUnmount() {
    window.removeEventListener("mousemove", this.onMousemove);
    window.removeEventListener("mouseup", this.onMouseup);
    window.removeEventListener("wheel", this.onMousewheel);
  },
  methods: {
    // 定位
    async location() {
      try {
        let response = await fetch(`https://restapi.amap.com/v3/ip?key=${KEY}`);
        let res = await response.json();
        let arr = res.rectangle.split(";");
        let pos1 = arr[0].split(",");
        let pos2 = arr[1].split(",");
        this.initCenter = this.center = [
          (Number(pos1[0]) + Number(pos2[0])) / 2,
          (Number(pos1[1]) + Number(pos2[1])) / 2,
        ];
      } catch (error) {
        console.log(error);
      }
    },

    // 初始化
    init() {
      // 获取容器宽高
      let { width, height } = this.$refs.mapBox.getBoundingClientRect();
      this.width = width;
      this.height = height;

      // 创建舞台
      this.stage = new Konva.Stage({
        container: this.$refs.mapBox,
        width,
        height,
      });

      // 创建两个图层
      this.layer1 = new Konva.Layer();
      this.layer1.x(this.width / 2).y(this.height / 2);
      this.stage.add(this.layer1);

      this.layer2 = new Konva.Layer();
      this.layer2.x(this.width / 2).y(this.height / 2);
      this.stage.add(this.layer2);
    },

    // 计算显示范围内的瓦片行列号
    renderTiles(isFadeIn = false) {
      // 中心点对应的瓦片
      let centerTile = getTileRowAndCol(
        ...lngLat2Mercator(...this.center),
        this.zoom
      );
      // 中心瓦片左上角对应的像素坐标
      let centerTilePos = [
        centerTile[0] * TILE_SIZE,
        centerTile[1] * TILE_SIZE,
      ];
      // 中心点对应的像素坐标
      let centerPos = getPxFromLngLat(...this.center, this.zoom);
      // 中心像素坐标距中心瓦片左上角的差值
      let offset = [
        centerPos[0] - centerTilePos[0],
        centerPos[1] - centerTilePos[1],
      ];
      // 计算瓦片数量
      let rowMinNum = Math.ceil((this.width / 2 - offset[0]) / TILE_SIZE);
      let colMinNum = Math.ceil((this.height / 2 - offset[1]) / TILE_SIZE);
      let rowMaxNum = Math.ceil(
        (this.width / 2 - (TILE_SIZE - offset[0])) / TILE_SIZE
      );
      let colMaxNum = Math.ceil(
        (this.height / 2 - (TILE_SIZE - offset[1])) / TILE_SIZE
      );
      // 渲染画布内所有瓦片
      this.currentTileCache = {}; // 清空缓存对象
      for (let i = -rowMinNum; i <= rowMaxNum; i++) {
        for (let j = -colMinNum; j <= colMaxNum; j++) {
          // 当前瓦片的行列号
          let row = centerTile[0] + i;
          let col = centerTile[1] + j;
          // 当前瓦片的显示位置
          let x = i * TILE_SIZE - offset[0];
          let y = j * TILE_SIZE - offset[1];
          // 缓存key
          let cacheKey = row + "_" + col + "_" + this.zoom;
          // 记录当前需要的瓦片
          this.currentTileCache[cacheKey] = true;
          // 该瓦片已加载过
          let layer = this.getCurrentMainLayer();
          if (this.tileCache[cacheKey]) {
            this.tileCache[cacheKey]
              .updateLayer(layer)
              .updatePos(x, y)
              .render(isFadeIn);
          } else {
            // 未加载过
            this.tileCache[cacheKey] = new Tile({
              layer,
              row,
              col,
              zoom: this.zoom,
              x,
              y,
              // 判断瓦片是否在当前画布缓存对象上，是的话则代表需要渲染
              shouldRender: (key) => {
                return this.currentTileCache[key];
              },
            });
          }
        }
      }
    },

    // 清空图层
    clearLayer() {
      this.layer1.removeChildren();
      this.layer2.removeChildren();
    },

    // 重置图层
    resetLayer() {
      // 如果当前元素显示在图层1，那么即将切换为图层2，所以将图层二的缩放复位、清空旧的元素、置顶显示，反之亦然
      let currentLayer = this.getCurrentMainLayer();
      let willLayer = this.useLayer1 ? this.layer2 : this.layer1;
      willLayer.scale({
        x: 1,
        y: 1,
      });
      willLayer.x(this.width / 2).y(this.height / 2);
      willLayer.removeChildren();
      currentLayer.zIndex(0);
      willLayer.zIndex(1);
    },

    // 鼠标按下
    onMousedown(e) {
      if (e.which === 1) {
        this.isMousedown = true;
      }
    },

    // 鼠标移动
    onMousemove(e) {
      if (!this.isMousedown) {
        return;
      }
      // 记录本次拖动的时间段及偏移量
      let curTime = Date.now();
      if (this.lastMouseTime) {
        this.lastDuration = curTime - this.lastMouseTime;
        this.lastDistance = [e.movementX, e.movementY]
      }
      this.lastMouseTime = curTime;
      // 计算本次拖动的距离对应的经纬度数据
      let mx = e.movementX * resolutions[this.zoom];
      let my = e.movementY * resolutions[this.zoom];
      let [x, y] = lngLat2Mercator(...this.center);
      // 更新拖动后的中心点经纬度
      this.center = mercatorToLngLat(x - mx, my + y);
      // 清除画布重新渲染瓦片
      this.clearLayer();
      this.renderTiles();
    },

    // 鼠标松开
    onMouseup() {
      this.isMousedown = false;
      this.momentum();
    },

    // 检查是否需要动量动画
    momentum() {
      if (this.lastDuration <= 0) {
        this.resetTranslate();
        return
      }
      // 计算最后时刻鼠标滑动的速度
      let speedX = this.lastDistance[0] / this.lastDuration;
      let speedY = this.lastDistance[1] / this.lastDuration;
      // 小于该速度则不进行动量动画
      if (Math.abs(speedX) <= 0.3 && Math.abs(speedY) <= 0.3) {
        this.resetTranslate();
        return
      }
      this.translate = [speedX / 0.005, speedY / 0.005];
      this.resetLayer();
      if (this.translatePlayback) {
        this.translatePlayback.stop();
      }
      this.translatePlayback = animate({
        from: this.translateTmp.join(" "),
        to: this.translate.join(" "),
        ease: easeOut,
        onUpdate: (latest) => {
          this.translateTmp = this.stringValueToArray(latest);
          let layer = this.getCurrentMainLayer();
          // 画布移动
          layer
            .x(this.width / 2 + this.translateTmp[0])
            .y(this.height / 2 + this.translateTmp[1]);
        },
        onComplete: () => {
          // 计算本次拖动的距离对应的经纬度数据
          let mx = this.translate[0] * resolutions[this.zoom];
          let my = this.translate[1] * resolutions[this.zoom];
          let [x, y] = lngLat2Mercator(...this.center);
          // 更新拖动后的中心点经纬度
          this.center = mercatorToLngLat(x - mx, my + y);
          this.useLayer1 = !this.useLayer1;
          this.resetTranslate();
          // 当前不在画布上的瓦片透明度都恢复成0
          this.hideTiles();
          // 重新渲染瓦片
          this.renderTiles();
        },
      });
    },

    // 鼠标滚动
    onMousewheel(e) {
      if (e.deltaY > 0) {
        this.zoomOut();
      } else {
        this.zoomIn();
      }
    },

    // 放大
    zoomIn() {
      this.scaleMap(true);
    },

    // 缩小
    zoomOut() {
      this.scaleMap(false);
    },

    // 缩放
    scaleMap(zoomIn) {
      if (!zoomIn) {
        // 层级变小
        if (this.zoom > this.minZoom) this.zoom--;
      } else {
        // 层级变大
        if (this.zoom < this.maxZoom) this.zoom++;
      }
      // 层级未发生改变
      if (this.lastZoom === this.zoom) {
        return;
      }
      this.lastZoom = this.zoom;
      // 更新缩放比例，也就是目标缩放值
      this.scale *= !zoomIn > 0 ? 0.5 : 2;
      // 停止上一次动画
      if (this.playback) {
        this.playback.stop();
      }
      // 图层重置
      this.resetLayer();
      // 开启动画
      this.playback = animate({
        from: this.scaleTmp, // 当前缩放值
        to: this.scale, // 目标缩放值
        duration: 500,
        onUpdate: (latest) => {
          // 实时更新当前缩放值
          this.scaleTmp = latest;
          let layer = this.getCurrentMainLayer();
          layer.scale({
            x: latest,
            y: latest,
          });
        },
        onComplete: () => {
          // 切换图层
          this.useLayer1 = !this.useLayer1;
          // 动画完成后将缩放值重置为1
          this.scale = 1;
          this.scaleTmp = 1;
          // 根据最终缩放值重新计算需要的瓦片并渲染
          this.renderTiles(true);
        },
      });
    },

    // 搜索
    onSearch() {
      clearTimeout(this.searchTimer);
      this.searchTimer = setTimeout(() => {
        if (this.searchText.trim()) {
          fetch(
            `https://restapi.amap.com/v3/assistant/inputtips?key=${KEY}&keywords=${this.searchText.trim()}`
          )
            .then((response) => {
              return response.json();
            })
            .then((response) => {
              this.searchResultList = response.tips || [];
            });
        } else {
          this.searchResultList = [];
        }
      }, 300);
    },

    // 定位到指定位置
    go(newCenter) {
      if (this.translatePlayback) {
        return;
      }
      if (typeof newCenter === "string") {
        newCenter = newCenter.split(",").map((item) => {
          return Number(item);
        });
      }
      // 目标位置经纬度转3857坐标
      let newCenterMercator = lngLat2Mercator(...newCenter);
      // 当前经纬度转3857坐标
      let centerMercator = lngLat2Mercator(...this.center);
      // 计算两者的距离，转换成像素
      this.translate = [
        (newCenterMercator[0] - centerMercator[0]) / resolutions[this.zoom],
        (newCenterMercator[1] - centerMercator[1]) / resolutions[this.zoom],
      ];
      // 重置画布
      this.resetLayer();
      // 开启动画
      this.translatePlayback = animate({
        from: this.translateTmp.join(" "),
        to: this.translate.join(" "),
        duration: 1000,
        onUpdate: (latest) => {
          this.translateTmp = this.stringValueToArray(latest);
          let layer = this.getCurrentMainLayer();
          // 画布移动
          layer
            .x(this.width / 2 - this.translateTmp[0])
            .y(this.height / 2 + this.translateTmp[1]);
        },
        onComplete: () => {
          // 中心点更新为目标经纬度
          this.center = newCenter;
          this.useLayer1 = !this.useLayer1;
          this.resetTranslate();
          // 当前不在画布上的瓦片透明度都恢复成0
          this.hideTiles();
          // 重新渲染瓦片
          this.renderTiles();
        },
      });
    },

    // 复位滑动变量
    resetTranslate() {
      this.translatePlayback = null;
      this.translateTmp = [0, 0];
      this.translate = [0, 0];
      this.lastMouseTime = null;
      this.lastDuration = 0;
      this.lastDistance = [];
    },

    // 当前不在画布上的瓦片透明度都恢复成0
    hideTiles() {
      Object.keys(this.tileCache).forEach((cacheKey) => {
        if (!this.currentTileCache[cacheKey]) {
          this.tileCache[cacheKey].hide();
        }
      });
    },

    // 空格分隔的字符串转数字数组
    stringValueToArray(latest) {
      return latest.split(" ").map((item) => {
        return Number(item);
      });
    },

    // 获取当前的主画布图层
    getCurrentMainLayer() {
      return this.useLayer1 ? this.layer1 : this.layer2;
    },

    // 回到初始位置
    backCenter() {
      this.go(this.initCenter);
    },

    // 切换经纬度
    onChangePos() {
      this.go(this.posValue);
    },
  },
};
</script>

<style scoped>
.map {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background-color: rgb(244, 244, 244);
  background-image: url(data:image/png;base64,);
  background-repeat: repeat;
}

.map * {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.mapBox {
  width: 100%;
  height: 100%;
}

.line {
  position: absolute;
  background-color: #000;
}

.lineX {
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 50px;
  height: 2px;
}

.lineY {
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  height: 50px;
  width: 2px;
}

.center {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-50%);
}

.searchBox {
  position: absolute;
  left: 20px;
  top: 20px;
  width: 200px;
}

.searchInput {
  width: 100%;
  height: 30px;
}

.searchInput input,
.posInput input {
  width: 100%;
  height: 100%;
  padding: 0 10px;
}

.searchList {
  position: absolute;
  left: 0px;
  top: 35px;
  width: 100%;
  background-color: #fff;
}

.searchList .searchItem {
  width: 100%;
  height: 30px;
  line-height: 30px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  padding: 0 10px;
  cursor: pointer;
}

.searchList .searchItem:hover {
  background-color: #f5f5f5;
}

.posInput {
  position: absolute;
  right: 20px;
  top: 20px;
  width: 300px;
  height: 30px;
}

.backCenterBtn {
  position: absolute;
  right: 20px;
  bottom: 20px;
  width: 50px;
  height: 50px;
  background-image: url("./assets/location.png");
  background-size: cover;
  cursor: pointer;
}

.scaleBtnBox {
  position: absolute;
  right: 30px;
  bottom: 80px;
  width: 29px;
  height: 73px;
  padding: 0 2px;
  background: #fff;
  border-radius: 3px;
}

.scaleBtn {
  width: 24px;
  height: 36px;
  margin: 0 auto;
  background: url("./assets/scale.png") no-repeat;
  cursor: pointer;
}

.scaleBtn.in {
  background-position: -3px 3px;
  border-bottom: 1px #dfdfdf solid;
}

.scaleBtn.out {
  background-position: -3px -156px;
}

.scaleBtn.disabled {
  opacity: 0.2;
}
</style>
